import { connect } from 'react-redux';
import Summary from '../components/summary';
import { updateDateRange, updateInterval, doTrendsStuff } from '../actions/summary';

const mapStateToProps = state => {
  const summary = state.summary;
  return {
    trendsData: summary.trendsData,
    trendsRequest: summary.trendsRequest,
    dateRange: summary.dateRange,
    isLoading: summary.isLoading,
    error: summary.error,
  };
};

const mapDispatchToProps = dispatch =>
  ({
    updateDateRange: dateRange => dispatch(updateDateRange(dateRange)),
    updateInterval: params => dispatch(updateInterval(params)),
    doTrendsStuff: newTrendsRequest => dispatch(doTrendsStuff(newTrendsRequest)),
  });

export default connect(mapStateToProps, mapDispatchToProps)(Summary);



