/**
 * Map camel case args to snakecase.
 * @param  {object} args
 * @param  {string} args.metric
 * @param  {string} args.dimension
 * @param  {string} args.startDate
 * @param  {string} args.endDate
 * @return {object}
 */
const toQueryArgs = ({ metric, dimension, startDate, endDate, interval }) => ({
  metric: metric,
  dimension: dimension,
  start_date: startDate,
  end_date: endDate,
  interval: interval,
});

export default toQueryArgs;



