/**
 *
 * @param  {object} trendsData
 * @return {object}
 */
const transformTrendsData = trendsData => {
  let dates = [];
  let transformedTrendsData = [];

  trendsData.forEach((datum, index) => {
    dates.push(datum.date.split(' ')[0]);
    transformedTrendsData.push({ x: index, y: datum.value });
  });

  return { dates, transformedTrendsData };
};

export default transformTrendsData;



