import React from 'react';
import PropTypes from 'prop-types';
import injectSheet from 'react-jss';
import { format } from 'd3-format';
import classNames from 'classnames';
import { withState } from 'recompose';
import {
  XYPlot,
  LineSeries,
  MarkSeries,
  XAxis,
  YAxis,
  VerticalGridLines,
  Crosshair,
  Hint,
  makeWidthFlexible
} from 'react-vis';
import { colors } from '../../helpers/constants';
import styles from '../css/summary/chart.css';

const FlexibleXYPlot = makeWidthFlexible(XYPlot);

const Chart = ({ yMax, dates, data, currentValue, updateCurrentValue, classes, metric }) => (
  <div className={classes.div}>
    <FlexibleXYPlot
      height={600}
      margin={{ left: 75, bottom: 110, right: 25 }}
      onMouseLeave={() => updateCurrentValue(() => null)}
      data-test-id="Chart"
      {...yMax === 0 ? { yDomain: [0, 100] } : {}}
    >
      <VerticalGridLines
        tickValues={data.map(datum => datum.x)}
      />
      <XAxis
        tickTotal={dates.length > 12 ? (dates.length > 200 ? dates.length / 10 : dates.length / 2) : dates.length}
        tickFormat={v => {
          if (v < 0 || typeof dates[v] === 'undefined') return null;
          return dates[v];
        }}
        tickLabelAngle={-60}
      />
      <YAxis
        tickFormat={tick => format('.2s')(tick)}
      />
      <LineSeries
        strokeWidth='4px'
        color={colors.chart[0]}
        onNearestX={value => updateCurrentValue(() => value)}
        data={data}
      />
      {currentValue && <MarkSeries data={[currentValue]} color={colors.banner} size={8} />}
      {currentValue &&
      <Hint value={currentValue}>
        <div className={classes.hint}>
          <div className={classNames([classes.hintTitle, classes.hintText])}>{dates[currentValue.x]}</div>
          <div className={classes.hintText}>
            <span className={classes.hintValue}>{currentValue.y.toLocaleString(undefined, { maximumFractionDigits: 0 })}</span>&nbsp;<br />
            {metric}
          </div>
        </div>
      </Hint>
      }
      <Crosshair
        values={[currentValue]}
        color='#000'
      >
            &nbsp;
      </Crosshair>
    </FlexibleXYPlot>
  </div>
);

Chart.propTypes = {
  yMax: PropTypes.number,
  dates: PropTypes.array,
  data: PropTypes.array,
  classes: PropTypes.object,
  currentValue: PropTypes.object,
  metric: PropTypes.string,
  updateCurrentValue: PropTypes.func
};

const withCurrentValue = withState('currentValue', 'updateCurrentValue', null);

export default withCurrentValue(injectSheet(styles)(Chart));



