import toQueryArgs from '../helpers/to-query-args';
import parseDateRange from '../helpers/parse-date-range';
import { loadingHelper } from './shared';

const _fetchTrendsData = (dispatch, { service, trendsRequest }) =>
  loadingHelper(dispatch, () =>
    service.fetchTrendsData(toQueryArgs(trendsRequest))
      .then(trendsData => dispatch(updateTrendsData(trendsData))));

const _updateTrendsRequestAndData = (dispatch, { trendsRequest, newTrendsRequest, service }) => {
  const updatedTrendsRequest = { ...trendsRequest, ...newTrendsRequest };
  return dispatch(updateTrendsRequest(updatedTrendsRequest))
    .then(() => _fetchTrendsData(dispatch, { service, trendsRequest: updatedTrendsRequest }));
};

export const updateTrendsRequest = trendsRequest => ({
  trendsRequest,
  type: 'UPDATE_TRENDS_REQUEST',
});

export const updateDateRangeString = dateRange => ({
  dateRange,
  type: 'UPDATE_DATE_RANGE_STRING',
});

export const updateTrendsData = trendsData => ({
  type: 'UPDATE_TRENDS_DATA',
  trendsData: trendsData.data,
});

export const doTrendsStuff = newTrendsRequest =>
  (dispatch, getState, { service }) =>
    _updateTrendsRequestAndData(dispatch, {
      service,
      newTrendsRequest,
      trendsRequest: getState().summary.trendsRequest,
    });

export const updateDateRange = dateRange => (dispatch, getState, { service }) => {
  let { startDate, endDate } = parseDateRange(dateRange);
  startDate = startDate.format('YYYY-MM-DD');
  endDate = endDate.format('YYYY-MM-DD');
  return dispatch(updateDateRangeString(dateRange))
    .then(() => _updateTrendsRequestAndData(dispatch, {
      service,
      trendsRequest: getState().summary.trendsRequest,
      newTrendsRequest: { startDate, endDate },
    }));
};

export const updateInterval = ({ interval, selectableDateRanges }) =>
  (dispatch, getState, { service }) => {
    const selectedDateRange = selectableDateRanges.filter(range => range.default === true)[0].range;
    let { startDate, endDate } = parseDateRange(selectedDateRange);

    startDate = startDate.format('YYYY-MM-DD');
    endDate = endDate.format('YYYY-MM-DD');

    return dispatch(updateDateRangeString(selectedDateRange))
      .then(() => _updateTrendsRequestAndData(dispatch, {
        service,
        trendsRequest: getState().summary.trendsRequest,
        newTrendsRequest: { interval, startDate, endDate }
      }));
  };



