import React from 'react';
import PropTypes from 'prop-types';
import injectSheet from 'react-jss';
import classNames from 'classnames';
import { INTERVAL_PICKER_VALUE_MAP, colors } from '../../helpers/constants';
const intervals = ['daily', 'weekly', 'monthly'];

const styles = {
  ul: {
    marginTop: '0.2em',
    '& li': {
      textTransform: 'uppercase',
      display: 'inline-block',
      padding: '.5em 1em',
      borderColor: colors.intervalPicker[1],
      border: '1px solid',
      borderLeft: 'none',
      backgroundColor: colors.intervalPicker[0],
      color: colors.text[0],
      fontWeight: 'bold',
      cursor: 'pointer',
    },
    '& li:first-child': {
      border: `1px solid ${colors.intervalPicker[1]}`,
    },
    '& li.current': {
      color: 'black',
      backgroundColor: 'white',
    },
  },
};

const IntervalPicker = ({ onClick, currentItem, classes }) => (
  <ul className={classes.ul}>
    {intervals.map((interval, i) => (
      <li
        className={classNames({ 'current': currentItem === interval })}
        key={i}
        onClick={() => onClick(interval)}
        data-test-id={`intervalPicker-${interval}`}
      >
        {INTERVAL_PICKER_VALUE_MAP[interval]}
      </li>
    ))}
  </ul>
);

IntervalPicker.propTypes = {
  onClick: PropTypes.func,
  currentItem: PropTypes.string,
  classes: PropTypes.object,
};

export default injectSheet(styles)(IntervalPicker);



