import React from 'react';
import injectSheet from 'react-jss';
import moment from 'moment';
import Cog from 'react-icons/lib/fa/cog';
import PropTypes from 'prop-types';
import { event } from '../helpers/ga';
import { trendsDimensions, trendsMetrics } from '../helpers/constants';
import selectableDateRanges from '../helpers/selectable-date-ranges';
import transformTrendsData from '../helpers/transform-trends-data';
import '../../../node_modules/react-vis/dist/style.css';
import styles from './css/summary.css';
import Button from './button';
import BrandHeader from './banner/brand-header';
import CustomPostsSelect from './selector/custom-post-select';
import MetricSelect from './selector/metric-select';
import TextDropDown from './selector/text-drop-down';
import BlockUI from './block-ui';
import Menu from './menu';
import IntervalPicker from './summary/interval-picker';
import Chart from './summary/chart';
import DisplayErrors from './display-errors';

class Summary extends React.PureComponent {
  constructor(props) {
    super(props);

    this.dateFormats = {
      daily: 'MMM D, YYYY',
      weekly: 'MMM D, YYYY',
      monthly: 'MMM YYYY',
    };
  }

  componentWillMount() {
    this.props.doTrendsStuff({});
  }

  render() {
    const {
      classes,
      path,
      updatePath,
      isEnabled,
      endDate,
      settingsUrl,
      trendsRequest,
      isLoading,
      trendsData,
      error,
      dateRange,
      updateDateRange,
      updateInterval,
      doTrendsStuff,
    } = this.props;

    const { dates, transformedTrendsData } = transformTrendsData(trendsData);

    const yMax = Math.max.apply(Math, transformedTrendsData.map(trendsDatum => trendsDatum.y));

    return (
      <div>
        <DisplayErrors
          error={error}
          showConnectionWarning={!isEnabled}
          lastFetch={endDate}
          settingsUrl={settingsUrl}
          formattedLastFetch={moment(endDate, 'YYYY-MM-DD').format(this.dateFormat)}
        />
        <Menu currentItem={path} updatePath={updatePath} disabled={isLoading} />
        <BrandHeader />
        <div className={classes.div}>
          <h3 className={classes.dateHeader}>
            Performance in
            <TextDropDown
              options={selectableDateRanges(trendsRequest.interval).map(range => ({ ...range, value: range.range, label: range.label.toLowerCase() }))}
              selectedOption={dateRange}
              onChange={dateRange => updateDateRange(dateRange)}
              dataTestId="dateSelect"
            />
          </h3>
          <div className={classes.filterBox}>
            <div style={styles.inline}>
              <div style={styles.selectorLabel}>Type</div>
              <CustomPostsSelect
                disabled={isLoading}
                selectRef={c => this.customPostSelect = c}
                dataTestId="customPostSelect"
                onChange={e => doTrendsStuff({ dimension: e.target.value })}
                customPost={trendsRequest.dimension}
                customPosts={Object.assign({}, trendsDimensions, this.props.customPostTypes)}
                style={styles.filterRightLabel}
              />
            </div>
            <div style={styles.inline}>
              <div style={styles.selectorLabel}>Metric</div>
              <MetricSelect
                disabled={isLoading}
                selectRef={c => this.metricSelect = c}
                dataTestId="metricSelect"
                onChange={e => doTrendsStuff({ metric: e.target.value })}
                metric={trendsRequest.metric}
                metrics={trendsMetrics}
              />
            </div>
            <div className={classes.filterRight}>
              <div style={styles.selectorLabel}>&nbsp;</div>
              <Button
                onClick={() => { event({ category: 'Settings', action: 'Open' }); window.open(this.props.settingsUrl, '_blank'); }}
                style={styles.noBorder}
              >
                <Cog />
              </Button>
            </div>
          </div>

          <div className={classes.chartBox}>
            <BlockUI className={classes.blockUi} text='No results for this selection. Please change the filters in the report title.' blocking={isLoading || !trendsData.length} spinner={isLoading} color='#4d83b8'/>
            <Chart
              data={transformedTrendsData}
              yMax={yMax}
              dates={dates.map(date => moment(date).format(this.dateFormats[trendsRequest.interval]))}
              metric={trendsMetrics[trendsRequest.metric]}
            />
            <div className={classes.intervalPicker}>
              <IntervalPicker
                currentItem={trendsRequest.interval}
                onClick={interval => updateInterval({ interval, selectableDateRanges: selectableDateRanges(interval) })}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Summary.propTypes = {
  service: PropTypes.object,
  classes: PropTypes.object,
  dateFormat: PropTypes.string,
  defaultDateRange: PropTypes.string,
  settingsUrl: PropTypes.string,
  dashboardUrl: PropTypes.string,
  path: PropTypes.string,
  updatePath: PropTypes.func,
  isEnabled: PropTypes.bool,
  endDate: PropTypes.string,
  trendsRequest: PropTypes.object,
  isLoading: PropTypes.bool,
  trendsData: PropTypes.array,
  error: PropTypes.string,
  dateRange: PropTypes.string,
  updateDateRange: PropTypes.func,
  updateInterval: PropTypes.func,
  doTrendsStuff: PropTypes.func,
  customPostTypes: PropTypes.object,
};

Summary.defaultProps = {
  dateFormat: 'm/d/Y',
  timeFormat: 'g:i A',
  defaultDateRange: '1-day',
  customPostTypes: { },
};

export default injectSheet(styles)(Summary);



